'use client'
import { useState } from 'react'

export default function Contact() {
  const [form, setForm] = useState({ name: '', email: '', message: '' })
  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) =>
    setForm({ ...form, [e.target.name]: e.target.value })
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    const subject = `Contacto de ${form.name}`
    const body = `Nombre: ${form.name}%0D%0ACorreo: ${form.email}%0D%0A%0D%0A${form.message}`
    window.location.href = `mailto:pokedev2025@gmail.com?subject=${encodeURIComponent(subject)}&body=${encodeURIComponent(body)}`
  }
  return (
    <section id="contact">
      <h2>Contacto</h2>
      <form onSubmit={handleSubmit}>
        <input name="name" value={form.name} onChange={handleChange} placeholder="Nombre" required />
        <input type="email" name="email" value={form.email} onChange={handleChange} placeholder="Correo" required />
        <textarea name="message" value={form.message} onChange={handleChange} placeholder="Mensaje" required />
        <button type="submit">Enviar</button>
      </form>
    </section>
  )
}
